.container{
  background-color: #333;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100vh;
}

textarea{
  font-size: 18px;
  padding: 10px 0 0 20px;
  /* margin: 26px 0; */
  box-sizing: border-box;
  font-family: monospace;
}

button{
  padding: 15px 30px; 
  margin-bottom: 15px;
  font-size: 20px;
  background-color: greenyellow;
  color: blue;
  font-weight: bolder;
  font-family: monospace;
  border: none;
}

button:hover{
  background-color: green;
  color: #fff;
  
}

.btnGroup{
  display: flex;
  flex-direction: column;
}